import { createRouter, createWebHistory } from "vue-router";
import Dicts from "../views/Dicts.vue";
import Districts from "../views/Districts.vue";
import SecuritySettings from "../views/SecuritySettings.vue";
import About from "../views/About.vue";
import Protocols from "../views/Protocols.vue";
import Reports from "../views/Reports.vue";
import Transfer from "../views/Transfer.vue";
import Deposits from "../views/Deposits.vue";

const routes = [
  {
    path: "/",
    name: "",
    component: Protocols,
  },
  {
    path: "/protocols",
    name: "",
    component: Protocols,
  },
  {
    path: "/transfer",
    name: " ",
    component: Transfer,
  },
  {
    path: "/deposits",
    name: "",
    component: Deposits,
  },
  {
    path: "/dicts",
    name: "",
    component: Dicts,
  },
  {
    path: "/reports",
    name: "",
    component: Reports,
  },
  {
    path: "/districts",
    name: "",
    component: Districts,
  },
  {
    path: "/about",
    name: " ",
    component: About,
  },
  {
    path: "/security",
    name: "",
    component: SecuritySettings,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.replace({ path: "*", redirect: "/" });

export default router;
